<template>
  <div class="p-4 cursor-pointer flex items-center select-none"
       @mouseenter="mouseEnterHandler"
       @mouseleave="mouseLeaveHandler"
       @click="handler">
    <div class="h-8 w-8 overflow-hidden">
      <transition name="fade" mode="out-in">
        <component :is="nightMode?'MoonIcon':'SunIcon'" class="fill-current h-8">
        </component>
      </transition>
    </div>

    <transition name="slide">
        <span class="px-4" v-if="nightMode">
          Night mode
        </span>
      <span class="px-4" v-else>
            Light mode
        </span>
    </transition>
  </div>

</template>

<script>
import MoonIcon from "./icons/MoonIcon";
import SunIcon from "./icons/SunIcon";

export default {
  name: "ThemeSwitcher",
  components: {
    SunIcon,
    MoonIcon,
  },
  data() {
    return {
      nightMode: null,
    }
  },
  methods: {
    handler: function () {
      this.nightMode = !this.nightMode;
    },
    mouseEnterHandler: function () {
      if (this.nightMode) {
        document.documentElement.classList.remove('dark')
      } else {
        document.documentElement.classList.add('dark')
      }
    },
    mouseLeaveHandler: function () {
      if (this.nightMode) {
        document.documentElement.classList.add('dark')
      } else {
        document.documentElement.classList.remove('dark')
      }
    }
  },
  watch: {
    nightMode: function (newValue) {
      if (newValue) {
        localStorage.theme = 'dark'
        document.documentElement.classList.add('dark')
      } else {
        localStorage.theme = 'light'
        document.documentElement.classList.remove('dark')
      }
    }
  },
  computed: {
    nightModeText: function () {
      if (this.nightMode) {
        return "Night mode";
      }
      return "Light mode";
    }
  },
  created() {
    this.nightMode = (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches))
  }
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
{
  opacity: 0;
}

.slide-leave-active,
.slide-enter-active {
  transition: 1s;
}

.slide-enter {
  transform: translate(100%, 0);
  transition: opacity .5s;
}

.slide-leave-to {
  transform: translate(-100%, 0);
  opacity: 0;
}
</style>